<template>
	<div id="city">
        <div class="city-head">
            <div class="header-search">
                <h2>{{text1}}</h2>
                <input type="text" :placeholder="inp1">
            </div>          
        </div>
    </div>
</template>
<script>
	export default {
		name:'city',
        data(){
            return{
                text1:"北京市",
                inp1:"选择城市、小区、写字楼、学校",
            }
        }
	}
</script>
<style lang="scss">
    @import "../assets/css/city.scss";
</style>


