import Vue from 'vue';
import Router from 'vue-router';
import allc from '../components/allc';
import classify from '../components/classify';
import mine from '../components/mine';
import addres from '../components/addres';
import isearch from '../components/isearch';
import loginin from '../components/loginin';
import regis from '../components/regis';
import delu from '../components/dl';
import cart from '../components/cart';
import order from '../components/order';
import forget from '../components/forget';
import city from '../components/city';
import store from '../components/store';
Vue.use(Router);
export default new Router({
  mode:"history",
  routes:[
    {
      path:"/",
      component: allc,
    },
    {
      path:"/allc",
      name:'allc',
      component: allc,
    },
    {
        path:'/allc/addres',
        name:'addres',
        component:addres
    },
    {
        path:'/allc/city',
        name:'city',
        component:city
    },
    {
        path:'/allc/isearch',
        name:'isearch',
        component:isearch
    },
    {
      path: "/classify",
      name: 'classify',
      component: classify
    },
    {
       path: '/cart',
       name: 'cart',
       component: cart  
    },
    {
       path: '/order',
       name: 'order',
       component: order  
    },
    {
      path: '/mine',
      name: 'mine',
      component: mine
    },
    {
      path: '/mine/loginin',
      name: 'loginin',
      component: loginin  
    },
    {
      path: '/mine/regis',
      name: 'regis',
      component: regis 
    },
    {
       path: '/allc/delu',
       name: 'delu',
       component: delu  
    },
    {
       path: '/mine/forget',
       name: 'forget',
       component: forget  
    },
    {
       path: '/allc/store',
       name: 'store',
       component: store  
    }
  ]
})



