<template>
	<div id="cart">
        <div class="cart-d">
            <div class="ul">
                <p>{{text1}}~<br>{{text2}}<br></p>
                <router-link to="/mine/loginin" class="ul-a">{{text3}}</router-link>
            </div>
        </div>
        <!-- end -->
        <div class="footer">
            <foot  v-for="(item,index) in footItems" :key="index" :className="item.className" :text="item.text" :to="item.to"/>
        </div>
    </div>
</template>
<script>
    import foot from "./allc/foot";
	export default {
        name:"cart",
        props:[],
        data:function(){
            return{
                text1:"购物车还空空如也哦",
                text2:"登录后将同步购物车中商品",
                text3:"立即登录",
                footItems:[
                   {className:"index",text:"首页",to:"/allc"},
                   {className:"classify",text:"分类",to:"/classify"},
                   {className:"cartList",text:"购物车",to:"/cart"},
                   {className:"orderList",text:"订单",to:"/order"},
                   {className:"mine",text:"我的",to:"/mine"}
                ],
            }
        },
        components:{
            foot
        }
	}
</script>
<style lang="scss">
	@import "../assets/css/cart.scss"
</style>



