<template>
    <!-- 订单页面 -->
	<div id="order">
        <div class="order-d">
    		<div class="ul">
    			<p>{{order.text1}}</p>
    			<router-link to="/mine/loginin" class="ul-a">{{order.text2}}</router-link>
    		</div>
    	</div>
    	<!-- end -->
        <div class="footer">
            <foot  v-for="(item,index) in footItems" :key="index" :className="item.className" :text="item.text" :to="item.to"/>
        </div>
    </div>
</template>
<script>
    import foot from "./allc/foot";
	export default {
        name:"order",
        props:[],
        data:function(){
            return{
                order:{
                    text1:"登录后才能查看订单哦",
                    text2:"立即登录"
                },
                footItems:[
                   {className:"index",text:"首页",to:"/allc"},
                   {className:"classify",text:"分类",to:"/classify"},
                   {className:"cartList",text:"购物车",to:"/cart"},
                   {className:"orderList",text:"订单",to:"/order"},
                   {className:"mine",text:"我的",to:"/mine"}
                ],
            }
        },
        components:{
            foot
        }
	}
</script>
<style lang="scss">
	@import "../assets/css/order.scss"
</style>



