<template>
	<div id="shops">
        <div class="head">
            <span class="head-a1" @click="bac">
                <span class="head-sp1"></span>
            </span>
            <div class="head-d"> 
                <span class="head-sp2"></span>
                <input type="text" :placeholder="search.inp">
            </div>
            <span class="head-sp3">{{search.text1}}</span>
        </div> 
        <div class="center">
            <div class="center-search">
                <div class="hot-search">
                    <div class="s1">{{search.text2}}</div>
                    <div class="s2">
                        <a href="" v-for="(item,index) in s2">{{item}}</a>
                    </div>
                </div>
                <div class="history">
                    <div class="history-search">{{search.text3}}</div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
	export default {
		name:"isearch",
        data(){
            return{
                search:{
                    text1:"搜索",
                    text2:"热门搜索",
                    text3:"搜索历史",
                    inp:"搜索附近的商店和门店",
                },
                s2:["西瓜","20减6","哈密瓜","尝鲜价","提子","新品尝鲜","桃","樱桃","爆品","特价","立秋","葡萄"]
            }
        },
        methods:{
            bac:function(){
                this.$router.go(-1);
            }
        }
	}
</script>
<style lang="scss">
	 @import "../assets/css/isearch.scss";
</style>


