<template>
	<router-link to="/allc/store" class="nearby-ul-li1" tag="li">
	    <div class="li1-d1">
	      <div class="d1-son">
	        <logo :src="shop.logo.src"/>
	        <div class="d1-son-d">
	          <a href="#" class="d1-son-da">
	            <div class="d1-da-dson">
	              <h2>{{shop.nears.text2}}</h2>
	              <div><span></span></div>
	            </div>
	            <tagbar :text="shop.tagbar.text1"/>
	            <star :num="shop.star.num1" />
	            <span class="d1-da-sp">{{shop.nears.text3}}</span>
	          </a>
	          <div>
	            <div class="d1-son-dd">
	              <div class="d1-son-dd1">
	                <a href="#" class="d1-dd-son">
	                  <ul>
	                    <activity v-for="(item,index) in shop.activitys" :key="index" 
	                    :className="item.className" :cont="item.cont" :text="item.text"/>
	                  </ul>
	                </a>
	              </div>
	              <div class="d1-son-dd2">
	                <tw v-for="(item,index) in shop.tws" :key="index" :src="item.src" :text="item.text" :price="item.price" />
	              </div>
	            </div>
	          </div>
	        </div>
	      </div>
	    </div>
	</router-link>   
</template>
<script>
    import tw from "./tw";
    import activity from "./activity";
    import star from "./star";
    import tagbar from "./tagbar";
    import logo from "./logo";
	export default{
		name:"dp",
		props:["shop"],
		components:{
		   tw,
	       activity,
	       logo,
	       star,
	       tagbar
		}
	}
</script>
<style></style>


