<template>
  <div id="main"> 
  <!--隐藏头部-->
    <div class="headery" v-if="!show1">
        <a href="" class="header-a">
            <span class="span-p">{{icons.text}}</span> 
            <span class="span-addr-right"></span>
        </a>
        <span class="d2"></span>
        <span class="ck"></span>
        <a class="tip" href="#"><span></span></a>
    </div>
    <!-- 头部 -->
    <div class="header" v-if="show1">
      <router-link class="header-a" :to="icons.to">
        <span class="span-p">{{icons.text}}</span>
        <span class="span-addr-right"></span>
      </router-link>
      <span class="span-left">
        <router-link :to="icons.tos" class="span-search-left"></router-link>
        {{icons.text1}}
      </span>
      <router-link to="/allc/delu" class="tip" href="#"><span></span></router-link>
    </div>
    <!-- swiper -->
    <div class="center">
      <div class="swiper-container" id="swiper-container1">
        <div class="swiper-wrapper">
          <div class="swiper-slide" v-for="(item,index) in swipers0" :key="index">
            <a href="#"><swiper-img :src="item.src"/></a>
          </div>
        </div>
        <div class="swiper-pagination"></div>
      </div> 
      <!-- product list -->
      <div class="product-list">
        <product-img v-for="(item,index) in productItems" :key="index" :imgurl="item.imgurl" :text="item.text"/>
      </div>
      <div class="wrapper">
        <a href="#"><img :src="pics.src"></a>
      </div>
        <!-- 限时秒杀 -->
      <div class="time-limit">
        <ul class="time-limit-left">
          <li class="limit1">
            <span class="limit1-s1"><img :src="tw1.src1"></span>
            <span class="limit1-s2"><img :src="tw1.src2"></span>
            <span class="limit1-s3">{{tw1.text}}</span>
            <a href="#"><img :src="tw1.src3"></a>
          </li>
          <tw1 v-for="(item,index) in tw1.tw1s" :key="index" :classN="item.classN" :className="item.className" :src="item.src" :text="item.text" :price="item.price" :href="item.href"/>
        </ul>
      </div>
      <!-- swiper轮播 -->
      <div class="swiper-switch"> 
        <div class="swiper-container" id="swiper-container2">
          <div class="swiper-wrapper">
            <div class="swiper-slide" v-for="(item,index) in swipers1" :key="index">
            <a href=""><swiper-img :src="item.src"/></a>
          </div>
          </div>
          <div class="swiper-pagination"></div>
        </div>
      </div>
      <!-- 附近的店铺 -->
      <div class="nearby-shop">
        <div class="nearby"><span>{{icons.txt1}}</span></div>
        <ul class="nearby-ul">
          <dp v-for="(shop,index) in lists" :key="index" :shop="shop" />
          <button @click="addMore()">{{icons.txt2}}</button> 
          <button @click="clear()">{{icons.txt3}}</button>
        </ul>
      </div> 
    </div>  
    <!-- end -->
    <div class="footer">
      <foot  v-for="(item,index) in footItems" :key="index" :className="item.className" :text="item.text" :to="item.to"/>
    </div>
  </div>
</template>
<script>
  import swiper from '../assets/swiper/swiper.min.js';
  import product_img from "./allc/product-img";
  import foot from "./allc/foot";
  import swiper_img from "./allc/swiper-img";
  import tw1 from "./allc/tw1";
  import icon from "./allc/icon";
  import dp from "./allc/dp";
  import $ from 'jquery';
  import store from '../assets/store/store.everything.min.js';
  export default{
     name:"allc",
     data:function(){
        return {
          show1:true,
          lists:[],
          icons:{
            text:"江宁大学城(..dfrgesdfzfds.)",
            text1:"附近商家商品",
            to:"/allc/addres",
            tos:"/allc/isearch",
            txt1:"附近的店铺",
            txt2:"加载更多",
            txt3:"刷新"
          },
          swipers0:[
            {src:"/static/uploads/switch1.jpg"},
            {src:"/static/uploads/switch2.jpg"}
          ],
          swipers1:[
            {src:"/static/uploads/wrapper1.png"},
            {src:"/static/uploads/wrapper2.png"}
          ],
          pics:{src:"/static/uploads/wrapper.png"},
          productItems:[
            {imgurl:"/static/img/list1.png",text:"超市生鲜"},
            {imgurl:"/static/img/list2.png",text:"新鲜水果"},
            {imgurl:"/static/img/list3.png",text:"零食小吃"},
            {imgurl:"/static/img/list4.png",text:"鲜花烘培"},
            {imgurl:"/static/img/list5.png",text:"医药健康"},
          ],
          footItems:[
           {className:"index",text:"首页",to:"/allc"},
           {className:"classify",text:"分类",to:"/classify"},
           {className:"cartList",text:"购物车",to:"/cart"},
           {className:"orderList",text:"订单",to:"/order"},
           {className:"mine",text:"我的",to:"/mine"}
          ],
          tw1:{
            text:"秒杀抢不停",
            src1:"/static/img/icon_qiang.png",
            src2:"/static/img/icon_miaosha.png",
            src3:"/static/uploads/1.png",
            tw1s:[
             {classN:"limit2",className:"limit2-sp",text:"满减不停",price:"龙眼8.8",src:"/static/img/2.png",href:"#"},
             {classN:"limit3",className:"limit3-sp",text:"坏菜包赔",price:"最高减15",src:"/static/img/3.png",href:"#"}
            ]
          }
        }
     },
     components:{
       "foot":foot,
       "product-img":product_img,
       "swiper-img":swiper_img,
       "tw1":tw1,
       "icon":icon,
       dp
     },
    created() {
        var th = this;
        var lists = store.get('lists');
        //判断当前是否有缓存
        if(lists){//如果有缓存遍历缓存
            th.lists = [];
            for (var i = 0; i < lists.length; i++) {
                th.lists.push(lists[i]);
            }
        }else{//否则通过异步获取最新数据后缓存
            $.get("http://easy-mock.com/mock/5a03168572fea73d00fc6c26/shop/lists", function (data) {
              console.log(data)
                store.set('lists', data);//缓存
                for (var i = 0; i < data.lists.length; i++) {
                    th.lists.push(data.lists[i]);
                }
            }, 'json');
        }
    },
    methods:{
      clear(){
        this.lists = [];//清空列表
        var th = this;
        store.clear();//清空缓存
        
        $.get("http://easy-mock.com/mock/5a03168572fea73d00fc6c26/shop/lists", function (data) {
            store.set('lists', data);
            for (var i = 0; i < data.lists.length; i++) {
                th.lists.push(data.lists[i]);
            }
            store.set('lists', th.lists);
        }, 'json');
      },
      addMore(){//获取新数据，更新缓存
          var th = this;
          $.get("http://easy-mock.com/mock/5a03168572fea73d00fc6c26/shop/lists", function (data) {
                store.set('lists', data);
                for (var i = 0; i < data.lists.length; i++) {
                    th.lists.push(data.lists[i]);
                }
                store.set('lists', th.lists);
          }, 'json');
      },
      handleScroll () {
        var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
        if(scrollTop>500){
          this.show1=false;
        }else{
          this.show1=true;
        }
      },
    },
     mounted () {
        var mySwiper = new Swiper('#swiper-container1', {
          autoplay: 3000,//可选选项，自动滑动
          loop:true,//环路
              pagination : '.swiper-pagination',
        })
        var mySwiper = new Swiper('#swiper-container2', {
          autoplay: 3000,//可选选项，自动滑动
          loop:true,//环路
              pagination : '.swiper-pagination',
        })
        window.addEventListener('scroll', this.handleScroll);
    },
  }
</script>
<style lang="scss">
  @import "../assets/css/index.scss";
</style>


