<template>
	<div id="addres">
    <!-- 定位路由 -->
    <router-link to="/allc/city" class="header"> 
      <input type="text" :placeholder="address.inp" disabled>
    </router-link>
    <div class="pos">
      <span></span>
      <p>{{address.text1}}</p>
    </div>
    <div class="addr">
      {{address.text2}}
      <br>
      {{address.text3}}
    </div>    
  </div>
</template>
<script>
	export default {
		name:'addres',
		data:function(){
            return{
               address:{
                  text1:"点击定位当前位置",
                  text2:"无法获取您的位置信息",
                  text3:"请手动输入地址",
                  inp:"选择城市、小区、写字楼、学校"
               }
            }
        }
	}
</script>
<style lang="scss">
  @import "../assets/css/addres.scss";
</style>


