<template>
    <!-- 注册页面 -->
	<div id="register">
    <!-- 注册头部 -->
        <div class="header">
           <router-link to="/mine/loginin" class="header-arrow"></router-link>
           <span class="header-dj">{{regis.txt1}}</span>
        </div>
        <!-- 中部 -->
        <div class="center">
            <div class="center-container">
                <div class="input-container">
                    <input type="text" :placeholder="regis.inp1" class="center-input1">
                    <span class="center-input1-sp">
                        <img src="/static/img/timg.jpg">
                    </span>
                </div>
                <!-- input -->
                <div class="input-box">
                    <div class="input-container">
                        <span class="input-box-sp">
                            <span class="input-box-sp1">{{regis.txt2}}</span>
                            <span class="input-box-sp2"></span>
                        </span>
                        <input type="text" :placeholder="regis.inp2" class="center-input2">
                    </div>
                    <button class="input-btn">{{regis.txt3}}</button>
                </div>
                <div class="input-container">
                    <input type="text" :placeholder="regis.inp3" class="center-input3">
                </div>
                <div class="input-container">
                    <input type="text" :placeholder="regis.inp4" class="center-input4">
                    <span class="psd-visible">
                        <span></span>
                    </span>
                </div>
                <!-- 提示 -->
                <div class="notice">
                    <span>{{regis.txt4}}</span>
                </div>
                <a href="#">{{regis.txt5}}</a>
            </div>
        </div>
    </div>
</template>
<script>
	export default {
		name:'regis',
        data:function(){
            return{
                regis:{
                    txt1:"京东注册",
                    txt2:"+86",
                    txt3:"获取验证码",
                    txt4:"密码由6-20位字母、数字或半角符号组成，不能是10位以下纯数字/字母/半角符号，字母需区分大小写",
                    txt5:"注 册",
                    inp1:"请输入图片验证码",
                    inp2:"请输入手机号",
                    inp3:"请输入手机验证码",
                    inp4:"请设置6-20位登录密码"
                }
            }
        }
	}
</script>
<style lang="scss">
	 @import "../assets/css/regis.scss";
</style>


