<template>
    <div>
	    <dl class="center-right-dl" v-for="(list,index) in good" :key="index">
	        <dt class="center-right-dt">{{list.info}}</dt>
	        <dd class="center-right-dd">
	          <ctw v-for="(item,index) in list.right_l" :key="index" :href="item.href" :src="item.src" :text="item.text"/>
	        </dd>
	    </dl>
    </div>
</template>
<script>
    import ctw from "./ctw";
	export default {
		name:"goodz",
		props:["good"],
		components:{
			ctw
		}
	}
</script>


