<template>
	<div id="dl">
        <div class="dl-d">
            <div class="ul">
                <p>{{text}}</p>
                <a class="ul-a">{{text2}}</a>
            </div>
        </div>
    </div>
</template>
<script>
	export default {
        name:"delu",
        data:function(){
            return{
                text:"请您登录",
                text2:"立即登录",
            }
        }
	}
</script>
<style lang="scss">
	@import "../assets/css/dl.scss"
</style>



