<template>
 <!-- 我的页面 -->
  <div id="login">
        <!-- 顶部 -->
        <div class="header">
            <!-- 登录注册路由 -->
            <router-link to="/mine/loginin" class="header-sp1">
                <span></span>
            </router-link>
            <router-link to="/mine/loginin" class="header-sp2"></router-link>
        </div>
        <div class="login-top">
            <span class="login-top-sp1"></span>
            <router-link to="/mine/loginin" class="login-top-sp2">{{logins.text}}</router-link>
        </div>
        <div class="login-top-d">
        <!-- 小图标 -->
        <ul class="login-top-ul">
            <navl v-for="(item,index) in navs" :key="index" :className="item.className" :ico="item.ico" :text="item.text" :href="item.href" />
        </ul>
        </div>
        <div class="login-pic">
            <img :src="logins.src">
        </div>
        <div class="login-list">
            <tub v-for="(item,index) in tubs" :key="index" :href="item.href" :src="item.src" :text="item.text"/>
        </div>
        <div class="close">
            <a href="#">{{logins.text2}}</a>
        </div>
        <!-- end -->
        <div class="footer">
          <foot  v-for="(item,index) in footItems" :key="index" :className="item.className" :text="item.text" :to="item.to"/>
        </div>
    </div>
</template>
<script>
  import swiper from '../assets/swiper/swiper.min.js';
  import foot from "./allc/foot";
  import navl from "./mine/navl";
  import tub from "./mine/tub";
  export default{
     name:"mine",
     // 数据
     data:function(){
        return {
          navs:[
           {className:"li-top-asp",ico:"",text:"我的钱包",href:""},
           {className:"li-top-aspb",ico:"--",text:"优惠券",href:""},
           {className:"li-top-aspb",ico:"--",text:"白条",href:""}
          ],
          logins:{text:"登录/注册",text2:"退出账号",src:"/static/uploads/l0.png"},
          tubs:[
           {src:"/static/img/l1.png",text:"我的关注",href:""},
           {src:"/static/img/l2.png",text:"我的评价",href:""},
           {src:"/static/img/l3.png",text:"我的地址",href:""},
           {src:"/static/img/l4.png",text:"退款/售后",href:""},
           {src:"/static/img/l5.png",text:"客服反馈",href:""},
           {src:"/static/img/l6.png",text:"帮助中心",href:""},
           {src:"/static/img/l7.png",text:"调查问卷",href:""},
           {src:"/static/img/l8.png",text:"办公室超市",href:""},
           {src:"/static/img/l9.png",text:"配送员招募",href:""},
           {src:"/static/img/l10.png",text:"关于我们",href:""}
          ],
          footItems:[
           {className:"index",text:"首页",to:"/allc"},
           {className:"classify",text:"分类",to:"/classify"},
           {className:"cartList",text:"购物车",to:"/cart"},
           {className:"orderList",text:"订单",to:"/order"},
           {className:"mine",text:"我的",to:"/mine"}
          ],
        }
     },
     // 组件
     components:{
       foot,
       navl,
       tub
     },
  }
</script>
<style lang="scss">
  @import "../assets/css/login.scss";
</style>


