        </div>
        <div class="center">
            <div class="center-d">
            <div class="center-d1">
                <label for="username">{{text2}}</label>
                <input id="username" class="username" type="text" :placeholder="text">
            </div>
            <div class="center-d2">
                <div class="center-d2son">
                    <img src="/static/img/init.gif">
                    <span>{{text3}}</span>
                </div>
            </div>
        </div>
        </div>
    </div>
</template>
<script>
	export default {
        name:"forget",
        data:function(){
            return{
                text:"用户名/邮箱/手机号",
                text1:"忘记密码",
                text2:"账号",
                text3:"点击进入下一步",
            }
        }
	}
</script>
<style lang="scss">
	@import "../assets/css/forget.scss"
</style>



